.btn-grey {
  background: #adaaaa;
  background-image: -webkit-linear-gradient(top, #adaaaa, #8c8c8c);
  background-image: -o-linear-gradient(top, #adaaaa, #8c8c8c);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#adaaaa), to(#8c8c8c));
  background-image: linear-gradient(to bottom, #adaaaa, #8c8c8c);
  border-radius: 4px;
  font-size: 1rem;
  padding: 8px 16px 8px 16px;
  border: none;
  text-shadow: 1px 1px 3px #666666;
  -webkit-box-shadow: 0px 1px 3px #666666;
  box-shadow: 0px 1px 3px #666666;
  font-family: Arial;
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
}

.btn-grey:hover {
  background: #858585;
  background-image: -webkit-linear-gradient(top, #858585, #737373);
  background-image: -o-linear-gradient(top, #858585, #737373);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#858585), to(#737373));
  background-image: linear-gradient(to bottom, #858585, #737373);
  text-decoration: none;
}


