.fixed-footer {
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(78, 80, 216, 0.888);
    /* background: rgba(78, 80, 216, 0.788); */
    /* bottom: 0px; */
    color: white;
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    font-size: 1.0rem;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: center;
        align-content: center;
    /* position: relative; */
    width: 100%;
    text-decoration: none;
    /* z-index: 999; */
    padding: 0 2%;
    -webkit-box-shadow: 0px -3px 10px 1px rgba(0, 0, 0, 0.71);
    box-shadow: 0px -3px 10px 1px rgba(0, 0, 0, 0.71);
    position: fixed;
    bottom: 0;
    font-family: var(--font-Montserrat);
}

.fixed-footer p {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 15px;
}

.fixed-footer a {
    color: white;
    text-decoration: none;
}

/* .fixed-footer {
    position: fixed;
    bottom: 0;
} */


/* Screens larger than 320 */
@media (min-width: 700px) {
    .fixed-footer {
        font-size: 1.2rem
    }
}


/* Screens larger than 320 */
@media (min-width: 1000px) {
    .fixed-footer {
        font-size: 1.4rem
    }
}



