.body-container {
  background-color: white;
  padding: 300px 20px;
}

#root:not(ParallaxHero) {
  /* background-color: var(--page-color-whitecap); */
  overflow-x: hidden;
  /* combined with the .main-container styling below (width: calc(100vw + 17px);), this pushes the right scrollbar off the screen without creating a new scrollbar on the bottom */
  /* selected the root id that the content is rendered into because most mobile browsers don't recognize overflow on body/html */
  /* Also added padding to the maincontainer right so content wasn't hidden on mobile */
}

.main-container {
  width: calc(100vw + 17px);
  padding: 0 17px 0 0;
}

.rental-results-div {
  width: 250px;
  display: grid;
  margin: auto;
  grid-gap: 30px;
  grid-template-columns: 250px;
}

.category-btn-container {
  width: 900px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 50px auto;
}

.rentals-by-date-form-div {
  border: 1px solid #000;
  display: -ms-flexbox;
  display: flex;
  width: 750px;
  margin: 50px auto;
  padding: 15px;
}

.rentals-by-date-form-div h4 {
  margin-bottom: 50px;
}

.see-rentals-form {
  width: 50%;
}



@media (min-width: 600px) {
  .rental-results-div {
    width: 530px;
    margin: auto;
    grid-gap: 30px;
    grid-template-columns: 250px 250px;
  }
}

@media (min-width: 860px) {
  .rental-results-div {
    width: 810px;
    margin: auto;
    grid-template-columns: 250px 250px 250px;
  }
}


